body,html #root{
  background-color:#EAEAEA;
  display: flex; 
  flex-direction: column;
  justify-content: space-between; 
  margin: 0;
  padding: 0;
 
  font-family: sans-serif;
 
}
.cart-content {
  display: flex; 
  justify-content: space-between; 
  gap: 20px; 
}

.cart-content-wr{
  display: flex;
  flex-direction: column; 
  justify-content: flex-start;
  align-items: flex-start; 
  gap: 10px;
  width: 100%; 
  padding: 0;
  margin: 0; 
  box-sizing: border-box;
}
.cart-main {
  display: flex;
 
  flex-direction: column; 
  justify-content: flex-start;
  min-height: 100vh;
  padding: 20px;
  margin: auto;
  padding: 20px;
  flex-grow: 1;
  margin: auto;
}

.cart-conent-main{

  padding: 300px;
 margin-top: -200px;
}
 .product-image-cart{
  align-items: center;
  max-width: 100%;
  height: 110px;
  margin-top: -2px;
  margin-left: -320px;
  border-radius: 12px;
  margin-bottom: 8px;
  }

  .product-card-cart:hover {
    transform: scale(1.05); 
  }
  .product-card-cart {
    position: relative;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 16px;
    width: 450px;
    flex-direction: column; 
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: transform 0.2s;
    margin: 16px 0;
  
  }

  .product-price-cart {
    position: absolute;
    color: #ffa600;
    font-size: 16px;
    font-weight: bold;
    margin-top: auto;
    margin: 0;
    bottom: 16px; 
  right: 16px;
  }
.product-price-cart-under{
  color: #c0bdbd;
  font-weight: lighter;
  margin-right: 50px;
  
}

  .product-name-сart{
    font-size: 15px;
    font-weight: bold;
    margin: 0;
    
    white-space: nowrap;
    margin-left:0px;
    margin-top:-80px;
    color: #302f2f;
  }
  

  .main-title{
    margin-left:  -20px;
    cursor: pointer;
  }
  .cart-tittle{
    margin-top: 140px;
    font-size: 20px;
    color: #302f2f;
    margin-right: 890px;

    position: absolute;
  
}
.header-catalog-cart{
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
  padding: 10px 10px; 
  margin: 0;
  margin-bottom: 50px;
}

  .fav-head,
  .cart-button {
    display: flex;
    align-items: center; 
    font-size: 18px;
    cursor: pointer;
    position: relative;
    background: none; 
    border: none; 
    margin: 0 10px; 
  }
  .actions-header {
    display: flex;
     align-items: center; 
margin-right: -75px;
   }

  .cart-actions {
    display: flex; 
    align-items: center;
    gap: 12px; 
    margin-top: 0px;
    border: none;
    margin-top: 16px; 
    justify-content: flex-start;
    margin-left: -320px;
    font-weight:bolder;
   
  }

  
  .btn-button {
    background-color: #FFCE7F;
    color: rgb(252, 248, 248);
    border: none;
    width: 30px; 
    height: 30px; 
    border-radius: 50%; 
    display: flex; 
    align-items: center; 
    justify-content: center;
    cursor: pointer;
    font-size: 20px;
    transition: background-color 0.3s;
    font-weight: bold;
    margin: 0 5px;
  }
  
  .btn-button:hover {
    background-color: #cacadf;
  }

  .delete-button {
  
    position: absolute;
    top: 8px;
    right: 8px;
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease, transform 0.2s ease;
    }
.delete-button:hover {
  
  transform: scale(1.05);
}
    footer {
      margin-top: auto;
      padding: 10px 0;
    }